import { useState, useEffect, RefObject, useCallback } from 'react';

interface MousePosition {
  x: number | null;
  y: number | null;
}

type CustomCallback = (x: number, y: number) => void | null;

const initialState = {
  x: null,
  y: null,
};

const useMousePointer = () => {
  const [customCallback, setCustomCallback] = useState<CustomCallback>(null);
  const [element, setElement] = useState<HTMLElement | null>(null);
  const [mousePosition, setMousePosition] = useState<MousePosition>(
    initialState
  );

  const isPointerInElement = (ref: RefObject<HTMLElement>) => {
    const object = ref.current;
    console.log({ object });
    return true;
  };

  const handleOnMouseMove = useCallback(
    (event: MouseEvent) => {
      const { clientX: x, clientY: y } = event;
      if (callback) {
        callback(x, y);
      }
      setMousePosition({ x, y });
    },
    [element]
  );

  useEffect(() => {
    document.addEventListener('mousemove', handleOnMouseMove);

    return () => {
      document.removeEventListener('mousemove', handleOnMouseMove);
    };
  }, [element]);

  return { isPointerInElement, mousePosition, setRef: setElement };
};

export default useMousePointer;
